<template>
    <div id="cessionedelquinto">
        <b-row class="top">
            <b-col lg><div class="leftTop"><img src="../assets/imgs/home/cdq.svg" alt="" class="imgBlock"></div></b-col>
            <b-col lg>
                <div class="rightTop">
                    <span class="title">CESSIONE DEL QUINTO</span><br> 
                    <span class="subtitle">Prestito senza pensieri?<br>Ci pensa <b>AFFIDA!</b> </span>
                </div>
            </b-col>
        </b-row>
        <div class="container">
            <b-row class="bottom">
                <b-col md class="left">
                    <span class="leftTitle"><b>AFFIDA</b>TI PER IL PRESTITO SENZA PENSIERI!</span>
                    <hr class="hrAffida" style="width: 60%; margin-left:0px;">
                    <p class="p1 p2">Per dipendenti e pensionati, permette di avere liquidità fino a 70.000 euro, con addebito diretto su stipendio/pensione, e dilazionabili da 2 a 10 anni !</p>
                    <p class="p1 p2">La cqs, rispetto ad un prestito personale, è molto conveniente perché ha un tasso di interesse più basso.</p>
                    <p class="p1 p2">Semplice, comoda, adatta anche a segnalati in banche dati.</p>
                    <p class="p1 p2">Contatta subito un consulente <b>Affida</b></p>

                </b-col>
                <b-col md class="right">
                    <div class="calcolatore">
                        <img src="../assets/imgs/logoBlack.png" alt="">
                        
                            <div class="titleSectionCalcolatore">
                                <div class="titleCalcolatore">Tipo Lavoro<span class="asterisco">*</span></div>
                                <div class="subTitleCalcolatore">Seleziona</div>
                            </div>

                            <div class="form-group">
                                <b-form-select v-model="tipoLavoroValue" :options="tipoLavoroOptions" @change="calculateFun"></b-form-select>
                                <small id="emailHelp" class="form-text text-muted">Tipo Lavoro</small>
                            </div>

                            <div class="titleSectionCalcolatore">
                                <div class="titleCalcolatore">Sesso<span class="asterisco">*</span></div>
                            </div>

                            <div class="form-group">
                                <b-form-select v-model="sessoValue" :options="sessoOptions" @change="calculateFun"></b-form-select>
                                <small id="emailHelp" class="form-text text-muted">Durata Mutuo</small>
                            </div>

                            <div class="titleSectionCalcolatore">
                                <div class="titleCalcolatore">Età<span class="asterisco">*</span></div>
                            </div>
                            <div class="d-flex justify-content-center">
                                <b-form-input id="range-2" v-model="etaValue" @change="calculateFun" type="range" min="18" max="87" step="1"></b-form-input>
                                <div class="valueRange">{{ etaValue }}</div>
                            </div>

                            <div class="titleSectionCalcolatore">
                                <div class="titleCalcolatore">Stipendio/Pensione Mensile (NETTO)<span class="asterisco">*</span></div>
                                <div class="subTitleCalcolatore">In Euro</div>
                            </div>

                            <div class="form-group">
                                <input class="form-control form-control-md field" v-model="stipendioMensileValue" @change="calculateFun" type="number" id="anticipo currency-field" placeholder="">
                                <small id="emailHelp" class="form-text text-muted">Stipendio/Pensione Mensile (Netto)</small>
                            </div>

                            <div class="titleSectionCalcolatore">
                                <div class="titleCalcolatore">Mensilità<span class="asterisco">*</span></div>
                                <div class="subTitleCalcolatore">In Mesi / Annui</div>
                            </div>

                            <div class="form-group">
                                <b-form-select v-model="mensilitaValue" :options="mensilitaOptions" @change="calculateFun"></b-form-select>
                                <small id="emailHelp" class="form-text text-muted">Mensilità</small>
                            </div>

                            <div class="titleSectionCalcolatore">
                                <div class="titleCalcolatore">Durata<span class="asterisco">*</span></div>
                                <div class="subTitleCalcolatore">In Mesi</div>
                            </div>

                            <div class="d-flex justify-content-center">
                                <b-form-input id="range-2" v-model="durataVal" @change="calculateFun" type="range" min="24" max="120" step="12"></b-form-input>
                                <div class="valueRange">{{ durataVal }}</div>
                            </div>

                            <div class="titleSectionCalcolatore">
                                <div class="titleCalcolatore">Importo Finanziabile</div>
                                <div class="subTitleCalcolatore">Indicativo</div>
                            </div>
                            <div class="rataVal"><span ><span id="ipotesiRata" class="count">{{ formatNumber(arrotondaNumero(ipotesiRata,2)) }}</span> €</span></div>
                        
                    </div>



                </b-col>
            </b-row>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            tipoLavoroOptions: ['Dipendente Statale','Dipendente Pubblico','Dipendente Privato','Dipendente Parapubblico','Dipendente Pensionato'],
            tipoLavoroValue: 'Dipendente Privato',
            sessoOptions: ['Maschio','Femmina','Altro'],
            sessoValue: 'Maschio',
            etaValue: '55',
            mensilitaOptions: ['12','13','14'],
            mensilitaValue: '',
            durataVal: '72',
            stipendioMensileValue: '',
            ipotesiRata: '',
        }
    },
    methods:{
        calculateFun(){
            var reddito = this.stipendioMensileValue * this.mensilitaValue / 12;
            this.ipotesiRata = (reddito/5) * this.durataVal;
        },
        arrotondaNumero(numero,nDecimali){
            var numeroArrotondato = Math.round(numero*Math.pow(10,nDecimali))/Math.pow(10,nDecimali);
            return numeroArrotondato;
        },
        formatNumber(n) {
            var ns = String(n);
            var splitted = ns.split('.');
            var first = String(splitted[0]);
            var changed = first.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ".")
            if(splitted[1]){
                n = changed+','+splitted[1];
            }else{
                n = changed;
            }
            return n;
        }
    }
}
</script>

<style scoped src="../assets/css/cdq.css">
    
</style>