<template>
    <div id="info">
        <div class="top">
            <img src="../assets/imgs/home/call.svg" alt="" class="imgBlock">
            
            <span class="title">CONTATTACI</span>
        </div>
        <div class="container">
            <span class="leftTitle">Hai bisogno di informazioni, per chiarimenti sulle nostre offerte o per una consulenza?</span>
            <p class="text">Scrivici o Cerca l’agenzia più vicina a te  un consulente <b>Affida</b> ti darà tutte le risposte di cui hai bisogno</p>
            
                <b-row class="icons">
                    <b-col>
                        <a href="tel:800957676">
                            <div class="infoCell leftInfo info">
                                <i class="material-icons-outlined infoIcon">local_phone</i>
                                <p>800 94 76 76</p>
                            </div>
                        </a>
                    </b-col>
                    <b-col>
                        <a href="mailto:info@affida.credit">
                            <div class="infoEmail leftInfo info">
                                <i class="material-icons-outlined infoIcon">email</i>
                                <p>info@affida.credit</p>
                            </div>
                        </a>
                    </b-col>
                </b-row>
                    
                    
                
                <ContactForm></ContactForm>
            
        </div>
        <p class="subtitle fancy"><span>LE AGENZIE <b>AFFIDA</b></span></p>      
        <div class="mappa">
            <div class="agenziaView" id="agenziaView">
                <span class="close" v-on:click="closeAgenzia()">X</span>
                <p id="spanName"></p>
                <p>
                    <span id="spanIndirizzo"></span> <span> - </span>
                    <span id="spanCitta"></span><span> (</span>
                    <span id="spanCap"></span><span>)</span>
                </p>
                <a target="_blank" href="https://www.google.com/maps/dir//Via+Lucrezio+Caro,+62,+00193+Roma+RM" id="linkA"><div class="link">Portami Qua</div></a>
            </div>
            <GmapMap :center="center" :zoom="6" style="width:100%;  height: 600px;" >
            <GmapMarker :key="index" v-for="(m, index) in agenzie" :position="{lat: Number(m.lat) ,lng: Number(m.lng)}" :title="m.nome" :icon="require('../assets/imgs/icona20x20.png')" @click="displayAgenzia(m)"></GmapMarker>
            
        </GmapMap>
        
        </div>  


    </div>
</template>

<script>
const axios = require('axios');
import ContactForm from '../components/contact-form'
export default {
  name: "Contattaci",
  components: {
    ContactForm,

  },
  data() {
    return {
      // default to Montreal to keep it simple
      // change this to whatever makes sense
      agenzie : null,
      center: { lat: 42.592477, lng: 12.909185 },
      places: [],
      currentPlace: null
    };
  },

  mounted() {
        this.geolocate();
        axios
            .get('https://affida.herokuapp.com/agenzie/getAll')
            .then(response => {
                this.agenzie = response.data.agenzie;
            })
  },

  methods: {
    // receives a place object via the autocomplete component
    setPlace(place) {
      this.currentPlace = place;
    },
    geolocate: function() {
      navigator.geolocation.getCurrentPosition(position => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        };
      });
    },
    displayAgenzia(agenzia){
        var agenziaView = document.getElementById("agenziaView");

        if(agenziaView.style.visibility == "hidden"){
            change(agenzia);
            agenziaView.style.visibility = "visible";
            agenziaView.style.opacity = "1";
        }else{
            this.closeAgenzia();
            change(agenzia);
            agenziaView.style.visibility = "visible";
            agenziaView.style.opacity = "1";
        }

        function change(agenzia){
            var nameView = document.getElementById("spanName");
            var indirizzoView = document.getElementById('spanIndirizzo');
            var cittaView = document.getElementById('spanCitta');
            var capView = document.getElementById('spanCap');
            var linkA = document.getElementById('linkA');
            nameView.innerHTML = agenzia.name;
            indirizzoView.innerHTML = agenzia.indirizzo;
            cittaView.innerHTML = agenzia.citta;
            capView.innerHTML = agenzia.cap;
            var ind_with_plus = agenzia.indirizzo.replace(/ /g,"+");
            ind_with_plus = ind_with_plus.replace(/,/g,"+");
            var link = "https://www.google.com/maps/dir//"+ind_with_plus+"+"+agenzia.citta.replace(/ /g,"+")+"+"+agenzia.cap;
            linkA.setAttribute("href",link);
        }

    },closeAgenzia(){
        var agenziaView = document.getElementById("agenziaView");
        agenziaView.style.visibility = "hidden";
        agenziaView.style.opacity = "0";
    }
  }
};
</script>

<style scoped>
#info{
    padding-top: 0px;
}
.agenziaView{
    transition: opacity 0.5s ease-out;
    visibility: hidden;
    opacity: 0;
    padding: 20px;
    background-color: rgba(255, 255, 255, 0.918);
    position: absolute;
    top: 60px;
    z-index: 1000;
    left: 10px;
}
.agenziaView > p{
    margin-bottom: 0px;
}

.link{
    margin-top: 10px;
    width: 100%;
    padding: 5px;
    text-align: center;
    border: 1px solid var(--complementaryC);
    background-color: white;
    font-size: 14pt;
    text-transform: uppercase;
}
.link:hover{
    background-color: var(--complementaryC);
    color: white;
}
a{
    text-decoration: none;
    color: black;
}

.close{
    position: absolute;
    right: 5px;
    top: 5px;
    cursor: pointer;
}

.top{
    max-width: 1200px;
    margin: 0 auto;
    text-align: center;
}

.scritte{
    position: absolute;
    top: 100px;
}

.title{
    color: var(--complementaryC);
    font-size: 10vw;
    font-weight: 900;
    line-height: 100px;
}

.imgBlock{
    margin-top: -180px;
    max-width: 100%;
}

.leftTitle{
    font-size: 30pt;
    line-height: 30px;
    font-weight: 400;
}
.leftTitle > b{
    font-weight: 700;
}

.container{
    text-align: center;
}
.text{
    font-size: 16pt;
}
@media (max-width: 1300px){
    .title{
        font-size: 12vw;
    }
    .imgBlock{
        margin-top: -180px;
    }
}
@media (max-width: 905px){
    .title{
        font-size: 15vw;
    }
    .imgBlock{
        margin-top: -10px;
    }
    .leftTitle{
        font-size: 17pt;
        line-height: 17px;
    }
    .text{
        font-size: 13pt;
    }
}

@media (max-width: 500px){
    .title{
        line-height: 48px;
        font-size: 15vw;
    }
    .imgBlock{
        margin-top: 0px;
    }
}

.leftInfo{
    text-align: center;
}

.infoIcon{
    font-size: 50pt;
}
.info > p{
    margin-top: -15px;
    font-size: 20pt;
    font-weight: 600;
}
.info{
    color: #6c757d;
}
.info:hover{
    color: var(--complementaryC);
}

.icons{
    margin-top: 50px;
}

.mappa{
    position: relative;
}


</style>