<template>
    <div id="mutui">
        <b-row class="top">
            <b-col lg><div class="leftTop"><img src="../assets/imgs/home/mutui.svg" alt="" class="imgBlock"></div></b-col>
            <b-col lg>
                <div class="rightTop">
                    <span class="title">MUTUI</span><br> 
                    <span class="subtitle">Realizzare i tuoi sogni? <br> con <b>AFFIDA puoi!</b> </span>
                </div>
            </b-col>
        </b-row>
        <div class="container">
            <b-row class="bottom">
                <b-col md class="left">
                    <span class="leftTitle">CON I MUTUI <b>AFFIDA</b>, PUOI!</span>
                    <hr class="hrAffida" style="width: 60%; margin-left:0px;">
                    <p class="p1">Acquisto prima o seconda casa, surroga, consolidamento?</p>
                    <p class="p1 p2"><b>Affida</b> è convenzionata con molti istituti bancari per fornirti tutte le soluzioni possibili e prospettarti soluzioni diverse in un solo appuntamento.</p>
                    <p class="p1 p2">I nostri consulenti saranno a tua disposizione dal primo appuntamento fino a dopo la stipula del mutuo per offrire a te ed alla tua famiglia la miglior soluzione e mantenerti informato sulle condizioni del mercato.</p>
                    <hr class="hr2">
                    <span class="leftTitle2">Devi ancora scegliere l’immobile dei tuoi sogni ?</span><br><br>
                    <p class="p1 p2">Attraverso la consulenza di un Personal Broker Affida potrai verificare la tua capacità reddituale e quindi conoscere l’importo massimo di mutuo che puoi ottenere prima ancora di aver scelto la casa dei tuoi sogni.</p>
                    <p class="p1 p2">La predelibera reddituale ti permetterà di scegliere la casa che fa per te e quando l’avrai trovata sarai già certo di potertela permettere, riducendo notevolmente i tempi per arrivare all’atto di compravendita!</p>
                </b-col>
                <b-col md class="right">
                    <span class="rightTitle">CALCOLA LA RATA</span>
                    <div class="calcolatore">
                        <img src="../assets/imgs/logoBlack.png" alt="">
                        <form class="formCalcolo">
                            <div class="titleSectionCalcolatore">
                                <div class="titleCalcolatore">Prezzo Immobile <span class="asterisco">*</span></div>
                                <div class="subTitleCalcolatore">In Euro</div>
                            </div>
                            <div class="form-group">
                                <input class="form-control form-control-lg field" v-model="prezzoImmobile" @change="calculateFun" type="number" id="prezzoImmobile currency-field" value=""  placeholder="Prezzo Immobile">
                                <small id="emailHelp" class="form-text text-muted">Prezzo Immobile</small>
                            </div>

                            <div class="titleSectionCalcolatore">
                                <div class="titleCalcolatore">Anticipo</div>
                                <div class="subTitleCalcolatore">In Euro</div>
                            </div>

                            <div class="form-group">
                                <input class="form-control form-control-lg field" v-model="anticipo" @change="calculateFun" type="number" id="anticipo currency-field" value="" placeholder="Anticipo">
                                <small id="emailHelp" class="form-text text-muted">Anticipo</small>
                            </div>

                            <div class="spanDiv">
                              <span class="spanName">Richiesta Mutuo</span>  
                              <span class="spanVal"><span id="richiestaMutuo" class="count">{{ formatNumber(richiestaMutuo) }}</span> Euro</span>
                            </div>
                             
                            <hr>

                            <div class="titleSectionCalcolatore">
                                <div class="titleCalcolatore">Durata <span class="asterisco">*</span></div>
                                <div class="subTitleCalcolatore">In Anni</div>
                            </div>

                            <div class="form-group">
                                <b-form-select v-model="durataSelected" :options="durataOptions" @change="calculateFun"></b-form-select>
                                <small id="emailHelp" class="form-text text-muted">Durata Mutuo</small>
                            </div>

                            <div class="titleSectionCalcolatore">
                                <div class="titleCalcolatore">Tipo di Tasso <span class="asterisco">*</span></div>
                                <div class="subTitleCalcolatore">Miglior Tasso Disponibile</div>
                            </div>

                            <div class="form-group">
                                <b-form-select v-model="tipoTassoSelected" :options="tipoTassoOptions" @change="calculateFun"></b-form-select>
                                <small id="emailHelp" class="form-text text-muted">Tipo di Tasso</small>
                            </div>

                            <div class="spanDiv">
                                <span class="spanName">Totale da Restituire</span> <span class="spanVal"><span id="totaleDaResistuire" class="count">{{ formatNumber(arrotondaNumero(totDaRestituire,2)) }}</span> Euro</span>
                            </div>
                            <hr>

                            <div>
                                <div class="titleSectionCalcolatore ts2">
                                    <div class="titleCalcolatore" style="text-align:left;">Tasso Applicato</div>
                                    <div class="subTitleCalcolatore">Miglior Tasso Disponibile</div>
                                </div>
                                <div style="float: right;"><span class="spanVal"><span id="tassoApplicato" class="count">{{tassoApplicato}}</span> %</span></div>
                            </div>
                            <hr style="margin-top: 80px;">
                            <div>
                                <div class="titleSectionCalcolatore ts2">
                                    <div class="titleCalcolatore">Interessi</div>
                                </div>
                                <div style="float: right;"><span class="spanVal"><span id="interessi" class="count">{{ formatNumber(arrotondaNumero(interessi,2))}}</span> Euro</span></div>
                            </div>

                            <hr style="margin-top: 60px;">

                            <div>
                                <div class="titleSectionCalcolatore ">
                                    <div class="titleCalcolatore">RATA Mensile</div>
                                    <div class="subTitleCalcolatore">Ipotesi Rata Mensile</div>
                                </div>
                                <div class="rataVal"><span id="ipotesiRata" class="count">{{ formatNumber(arrotondaNumero(rataMensile,2))}}</span> €</div>
                            </div>

                            <a href="/trasparenzaCalcolatori"><div class="trasparenza"><i class="material-icons-outlined infoIcon">info</i> <span class="infoScritta">TRASPARENZA</span></div></a>

                        </form>
                    </div>
                </b-col>
            </b-row>
        </div>

        <div class="block"></div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            prezzoImmobile: '',
            anticipo: '',
            richiestaMutuo: '',
            totDaRestituire: '',
            tassoApplicato: 3.7,
            interessi: '',
            rataMensile: '',
            durataOptions: ['10','15','20','25','30'],
            durataSelected: '10',
            tipoTassoOptions: ['Variabile','Fisso'],
            tipoTassoSelected: 'Variabile',
        }
    },
    methods:{
        calculateFun(){
            this.richiestaMutuo = this.prezzoImmobile - this.anticipo;
            var saggioInteresse = this.tassoApplicato/100/12;
            var mensilità = this.durataSelected * 12;
            var top = this.richiestaMutuo * saggioInteresse;
            var t1 = 1 + saggioInteresse;
            var t2 = Math.pow(t1,-mensilità);
            var bottom = 1-t2;
            this.rataMensile = top/bottom;
            this.totDaRestituire = this.rataMensile * mensilità;
            this.interessi = this.totDaRestituire - this.richiestaMutuo;
        },
        arrotondaNumero(numero,nDecimali){
            var numeroArrotondato = Math.round(numero*Math.pow(10,nDecimali))/Math.pow(10,nDecimali);
            return numeroArrotondato;
        },
        formatNumber(n) {
            var ns = String(n);
            var splitted = ns.split('.');
            var first = String(splitted[0]);
            var changed = first.replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, ".")
            if(splitted[1]){
                n = changed+','+splitted[1];
            }else{
                n = changed;
            }
            return n;
        }
    }
    

    
}
</script>

