import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Mutui from '../views/Mutui.vue'
import Prestiti from '../views/Prestiti.vue'
import CessioneDelQuinto from '../views/Cessionedelquinto.vue'
import Contattaci from '../views/Contattaci.vue'
import PrivacyPolicy from '../views/PrivacyPolicy.vue'
import Trasparenza from '../views/Trasparenza.vue'
import TrasparenzaCalcolatori from '../views/TrasparenzaCalcolatori.vue'
import AreaRiservata from '../views/AreaRiservata.vue'
import AreaRiservataLogin from '../views/area_riservata/Login.vue'
import Admin from '../views/Admin.vue'
import AdminLogin from '../views/admin/AdminLogin.vue'
import AdminAgenti from '../views/admin/AdminAgenti.vue'
import AdminListaAccessi from '../views/admin/AdminListaAccessi.vue'
Vue.use(VueRouter)


const routes = [
  { path: '/', name: 'Home', component: Home},
  { path: '/Home', redirect:'/'},
  { path: '/home', redirect:'/'},
  { path: '/index', redirect:'/'},
  { path: '/index.html', redirect:'/'},
  { path: '/mutui', name: 'Mutui', component: Mutui},
  { path: '/prestiti', name: 'Prestiti', component: Prestiti},
  { path: '/cessionedelquinto', name: 'Cessione Del Quinto', component: CessioneDelQuinto},
  { path: '/contattaci', name: 'Contattaci', component: Contattaci},
  { path: '/privacy-policy', name: 'Privacy Policy', component: PrivacyPolicy},
  { path: '/trasparenza', name: 'Trasparenza', component: Trasparenza},
  { path: '/trasparenzacalcolatori', name: 'Trasparenza', component: TrasparenzaCalcolatori},
  { path: '/admin', name: 'Admin', component: Admin,
      children:[
        {path: 'login', name: 'Admin - Login',component: AdminLogin},
        {path: 'agenti', name: 'Admin - Agenti',component: AdminAgenti},
        {path: 'lista-accessi', name: 'Admin - Lista Accessi',component: AdminListaAccessi},
  ]},
  { path: '/area-riservata', name:'Area Riservata', component: AreaRiservata,
      children:[
        {path: 'login', name:'Area Riservata - Login', component: AreaRiservataLogin}
      ]}
]

const router = new VueRouter({
  mode: 'history',
  routes,
})

export default router
