<template>
    <div id="admin">
        <AdminSidebar></AdminSidebar>
        <router-view/>
    </div>

</template>

<script>
import AdminSidebar from '../components/admin/adminSidebar'
export default {
    data(){
        return{
            logged: true,
        }
    },
    components: {
        AdminSidebar,
    },
    mounted(){
        if(this.logged == false && this.$route.name != 'Admin - Login'){
            window.location.href = "/admin/login";
        }
    }
}
</script>

