<template>
    <div id="displayAgente">
        <b-modal :id="agente._id.toString()" :title="capitalize(agente.nome) + ' ' + capitalize(agente.cognome)" hide-footer @hidden="onHidden">
            
            <div class="preIMG">
                <img :src="photo" alt="" class="imgProfile">
            </div>
            <div class="stats" v-if="!editMode">
                <p class="info"><b>Email:</b> {{agente.email}}</p>
                <p class="info"><b>Stato Account:</b> {{agente.status=="pending"?'Da Confermare':'Confermato' }}</p>
                <p class="info"><b>Ruolo:</b> {{capitalize(agente.ruolo)}}</p>
                <p class="info"><b>Figura:</b> {{capitalize(agente.figura)}}</p>
                <p class="info"><b>Data Inizio:</b> {{dataInizio.getDate()}}/{{dataInizio.getMonth()+1}}/{{dataInizio.getFullYear()}}</p>
                <p class="info"><b>Indirizzo:</b> {{capitalize(agente.indirizzo)}}</p>
                <p class="info"><b>Città(Provincia):</b> {{capitalize(agente.citta)}} ({{capitalize(agente.provincia)}})</p>
                <p class="info"><b>Codice Fiscale:</b> {{agente.codiceFiscale}}</p>
                <p class="info"><b>Partita Iva:</b> {{agente.partitaIva}}</p>
                <p class="info"><b>Cellulare:</b> {{agente.cellulare}}</p>
                <p v-if="agente.topExecutive" class="info"><b>Top Executive</b> {{agente.topExecutive}}</p>
                <p v-if="agente.executive" class="info"><b>Executive</b> {{agente.executive}}</p>
                <p v-if="agente.teamLeader" class="info"><b>Team Leader</b> {{agente.teamLeader}}</p>
                <p v-if="agente.votazioni[0]" class="info"><b>Votato Questo Mese:</b> {{agente.votazioni[0].voto_effettuato?'Si':'No'}}</p>

                <a href=""><div class="btnProfile publicProfileBtn">PUBLIC PROFILE</div></a>
                <div class="btnProfile editBtn" @click="editMode = true">MODIFICA</div>
            </div>

            <div class="editMode" v-if="editMode">

                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Email:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-input v-model="agente.email" size="sm" class="inputProfile" required></b-form-input></b-col>
                    </b-row>
                </p>
                        
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Stato Account:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-select v-model="selectedStato" :options="optionsStato" size="sm" class="mt-3" required></b-form-select></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Ruolo:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-select v-model="selectedRuolo" :options="optionsRuolo" size="sm" class="mt-3" required></b-form-select></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Figura:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-select v-model="selectedFigura" :options="optionsFigura" size="sm" class="mt-3" required></b-form-select></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Data Inizio:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-datepicker id="example-datepicker" v-model="dateValue" size="sm" class="mt-3"></b-form-datepicker></b-col>
                    </b-row>
                </p>
                
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Indirizzo:</b> </b-col>
                        <b-col class="inpcol" sm="8"><b-form-input v-model="agente.indirizzo" size="sm" class="inputProfile"></b-form-input></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Città(Provincia):</b> </b-col>
                        <b-col class="inpcol" sm="8">
                            <b-row class="inprow">
                                <b-col class="inpcol"><b-form-input v-model="agente.citta" size="sm" class="inputProfile"></b-form-input></b-col>
                                <b-col class="inpcol"><b-form-input v-model="agente.provincia" size="sm" class="inputProfile"></b-form-input></b-col>
                            </b-row>
                        </b-col>
                    </b-row>
                    
                    
                    
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Codice Fiscale:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-input v-model="agente.codiceFiscale" size="sm" class="inputProfile"></b-form-input></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Partita Iva:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-input v-model="agente.partitaIva" size="sm" class="inputProfile"></b-form-input></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Cellulare:</b></b-col>
                        <b-col class="inpcol" sm="8"><b-form-input v-model="agente.cellulare" size="sm" class="inputProfile"></b-form-input></b-col>
                    </b-row>
                </p>
                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Top Executive</b></b-col>
                        <b-col class="inpcol" sm="8">
                            <select v-model="selectedTopExecutive" class="mt-3 custom-select custom-select-sm">
                                <option :key="agente._id" v-for="agente in this.$parent.agentiTopExecutive">{{agente.email}}</option>
                            </select>
                        </b-col>
                    </b-row>
                </p>

                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Executive</b></b-col>
                        <b-col class="inpcol" sm="8">
                            <select v-model="selectedExecutive" class="mt-3 custom-select custom-select-sm">
                                <option :key="agente._id" v-for="agente in this.$parent.agentiExecutive">{{agente.email}}</option>
                            </select>
                        </b-col>
                    </b-row>
                </p>

                <p class="info">
                    <b-row class="inprow">
                        <b-col class="inpcol" sm="4"><b>Team Leader</b></b-col>
                        <b-col class="inpcol" sm="8">
                            <select v-model="selectedTeamLeader" class="mt-3 custom-select custom-select-sm">
                                <option :key="agente._id" v-for="agente in this.$parent.agentiTeamLeader">{{agente.email}}</option>
                            </select>
                        </b-col>
                    </b-row>
                </p>

                <p v-if="agente.votazioni[0]" class="info"><b>Votato Questo Mese:</b> {{agente.votazioni[0].voto_effettuato?'Si':'No'}}</p>

                <div class="btnProfile annullaBtn" @click="editMode = false">ANNULLA</div>
                <div class="btnProfile editBtn" >APPLICA</div>
            </div>
        
        </b-modal>
    </div>
</template>

<script>
export default {
    data(){
        return{
            dataInizio: new Date(),
            photo:'',
            editMode: false,
            selectedFigura: '',
            optionsFigura: ["head executive","top executive","executive","team leader","personal broker","backoffice"],
            optionsRuolo:["agente","dipendente"],
            selectedRuolo: '',
            optionsStato:['Da Confermare','Confermato','Cancellato'],
            selectedStato: '',
            dateValue: '',
            selectedTopExecutive: '',
            selectedExecutive: '',
            selectedTeamLeader: '',


        }
    },
    props: ['agente','index'],
    mounted(){
        this.dataInizio = new Date(this.agente.dataInizio);
        this.dateValue = new Date(this.agente.dataInizio);

        this.selectedFigura = this.agente.figura;
        this.selectedRuolo = this.agente.ruolo;
        if(this.agente.status == 'pending'){this.selectedStato = 'Da Confermare'}
        if(this.agente.status == 'validated'){this.selectedStato = 'Confermato'}
        if(this.agente.status == 'deleted'){this.selectedStato = 'Cancellato'}
        if(this.agente.urlPhoto){this.photo = this.agente.urlPhoto}else{
            this.photo = require('../../../assets/imgs/empty-profile.png');
        }
    },methods:{
        capitalize(s){
            if (typeof s !== 'string') return ''
            return s.charAt(0).toUpperCase() + s.slice(1)
        },
        onHidden() {
            this.editMode = false;
            console.log('chiuso');
        }
    }
}
</script>
<style scoped>
    .info{
        margin-top: 5px;
        margin-bottom: 5px;
    }

    .preIMG{
        text-align: center;
        width: 100%;
        margin-bottom: 10px;
    }
    .imgProfile{
        max-width: 50%;
    }

    .btnProfile{
        border: 1px solid;
        width: max-content;
        padding: 5px 10px 5px 10px;
        margin-bottom: 5px; 
        margin-right: 5px;
        float: left;
        font-weight: 600;
    }
    a:hover{
        text-decoration: none;
    }

    .publicProfileBtn{
        border-color: var(--complementaryC);
        color: var(--complementaryC);
    }
    .publicProfileBtn:hover{
        background-color: var(--complementaryC);
        color: white;
    }
    .editBtn{
        border-color: rgb(68, 68, 255);
        color: rgb(68, 68, 255);
        cursor: pointer;
    }
    .editBtn:hover{
        background-color: rgb(68, 68, 255);
        color: white;
    }
    .annullaBtn{
        border-color: gray;
        cursor: pointer;
    }
    .annullaBtn:hover{
        background-color: gray;
        color: white;
    }

    .inpcol{
        padding-left: 0px;
        padding-right: 0px;
    }
    .inprow{
        padding-left: 15px;
        padding-right: 15px;
    }
    .mt-3{
        margin-top: 0px!important;
    }

</style>