<template>
    <div id="adminAgenti">
        <p class="subtitle fancy"><span>LISTA <b>AGENTI</b></span></p>
        <div class="container">
                <template v-if="loading">
                    <b-row>
                        <b-col sm="6" class="listColumn" v-for="index in 50" :key="index"><b-skeleton animation="wave" class="skeleton" width="100%"></b-skeleton></b-col>

                    </b-row>
                </template>


            <div class="filters">
                <b-row class="my-1">
                    <b-col sm="6">
                        <label for="input-small" class="overInput">Name o Cognome:</label>
                        <b-form-input id="input-small" size="sm" placeholder="Inserisci Nome o Cognome" v-model="nameFilterInput" @change="Filtring"></b-form-input>
                    </b-col>
                    <b-col sm="6">
                        <label for="input-small" class="overInput">Figura:</label>
                        <b-form-select size="sm" v-model="figuraSelected" :options="figuraOptions" @change="Filtring"></b-form-select>                
                    </b-col>
                </b-row>
            </div>

            <transition name="fade">
                <b-list-group class="list" v-if="!loading">
                    <b-row>
                        <b-col md="6" class="listColumn" :key="index" v-for="(user,index) in agentiFiltrati">
                            <b-list-group-item button  v-b-modal="user._id.toString()" class="item">
                                <img src="../../assets/imgs/empty-profile.png" alt="" class="imgProfile">
                                <span class="spanNome">{{user.nome}}</span> <span class="spanCognome">{{user.cognome}}</span>  <span class="spanFigura">{{user.figura}}</span>
                                <DisplayAgente :agente=user :index=index></DisplayAgente>
                            </b-list-group-item>
                        </b-col>
                    </b-row>
                    
                </b-list-group>
            </transition>
        </div>


    </div>
</template>

<script>
const axios = require('axios');
import DisplayAgente from "../../components/admin/adminAgenti/displayAgente";
export default {
    data(){
        return{
            agentiTotali: [],
            agentiFiltrati: [],
            agentiTopExecutive: [],
            agentiExecutive: [],
            agentiTeamLeader: [],
            nameFilterInput: '',
            figuraOptions: ["Tutti","head executive","top executive","executive","team leader","personal broker"],
            figuraSelected: "Tutti",
            loading: false,
        }
    },
    components: {
        DisplayAgente,
    },
    mounted(){
        this.loading = true,
        axios
            .get('https://affida.herokuapp.com/user/getAll')
            .then(response => {
                this.agentiTotali = response.data.users;
                this.agentiFiltrati = this.agentiTotali;
                this.agentiExecutive = this.agentiTotali.filter(this.filterFigura.bind(null,'executive'));
                this.agentiTeamLeader = this.agentiTotali.filter(this.filterFigura.bind(null,'team leader'));
                this.agentiTopExecutive = this.agentiTotali.filter(this.filterFigura.bind(null,'top executive'));
                this.loading = false;
            })
    },
    methods:{
        filterName(word,element){
            return element.nome.includes(word) || element.cognome.includes(word);
        },
        filterFigura(figura,element){
            return element.figura == figura;
        },
        Filtring(){
            this.agentiFiltrati = this.agentiTotali.filter(this.filterName.bind(null,this.nameFilterInput));
            if(this.figuraSelected != 'Tutti'){
               this.agentiFiltrati = this.agentiFiltrati.filter(this.filterFigura.bind(null,this.figuraSelected)); 
            }
            
        },
        capitalize(s){
            if (typeof s !== 'string') return ''
            return s.charAt(0).toUpperCase() + s.slice(1)
        }
    }
    
}
</script>

<style scoped>
    #adminAgenti{
        margin-top: calc(var(--NavBarHeight) + 30px);   
    }
    .filters{
        box-shadow: 0 2px 2px 0px rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);;
        width: 100%;
        background-color: white;
        text-align: center;
        padding: 0px 20px 10px 20px;
        max-width: 1000px;
        position: fixed;
        left: 50%;
        bottom: 0px;
        transform: translateX(-50%);
        z-index: 3;
        border: 2px solid var(--complementaryC);
        border-bottom: 0px;
    }
    .list{
        margin-top: 10px;
    }

    .listColumn{
        padding-left: 5px;
        padding-right: 5px;
        margin-bottom: 5px;
    }

    .overInput{
        margin-bottom: 0px;
        margin-top: 10px;
    }

    .item{
        border-radius: 10px;
        z-index: 2;
    }
    .skeleton{
        height: 50px;
        background-color: white;
        border: 1px solid rgba(0,0,0,.125);
        border-radius: 10px;
        cursor: pointer;
        z-index: 1;
    }
    .b-skeleton-animate-wave::after{
        background: linear-gradient(90deg, transparent, rgba(255, 229, 156, 0.4), transparent);
    }
    
    .fade-enter-active, .fade-leave-active {
        transition: opacity .5s;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }

    .imgProfile{
        max-height: 40px;
        max-width: 40px;
        border: 2px solid rgba(0,0,0,.125);
        margin-right: 10px;
    }
    .spanNome, .spanCognome{
        text-transform: uppercase;
        font-weight: 500;
    }
    .spanFigura{
        text-transform: uppercase;
        font-size: 8pt;
    }
    .spanFigura::before{
        content: ' - ';
    }
    button:focus{
        outline: none!important;
    }
</style>